import React, { useState, useEffect } from "react";
import PropTypes from "prop-types";
import { getPosts } from "../../WebAPI";
import styled from "styled-components";
import { Link } from "react-router-dom";

const Root = styled.div`
  width: 60%;
  margin-top: 80px;
  text-decoration: none;
  text-align: center;
  @media (max-width: 1250px) {
    width: 100%;
  }
  @media (max-width: 660px) {
    margin-top: 200px;
  }
`;
const Loading = styled.div`
  z-index: 999;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  color: white;
  font-size: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
`;
const PostContainer = styled.div`
  padding: 16px;
  display: flex;
  justify-content: space-around;
  margin: 20px auto;
  cursor: pointer;
  border-radius: 5px;
  color: rgba(255, 255, 255, 0.3);
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  @media (max-width: 1250px) {
    flex-direction: column;
  }
`;

const PostTitle = styled(Link)`
  font-size: 24px;
  width: 400px;
  text-align: left;
  text-decoration: none;
  color: rgba(255, 255, 255, 0.5);
  :hover {
    color: white;
    transition-duration: 0.5s;
  }
  @media (max-width: 1250px) {
    flex-direction: column;
    margin-left: 50px;
  }
`;

const PostDate = styled.div`
  font-size: 12px;
  padding: 0px;
  margin-top: 15px;
`;
const PageSign = styled.div`
  margin: 20px 20px;
  display: inline-block;
  padding: 10px;
  color: white;
  font-size: 10px;
`;
const Button = styled.button`
  color: white;
  border: 1px solid rgba(255, 255, 255, 0.2);
  background: none;
  cursor: pointer;
  padding: 10px;
  margin: 10px;
  border-radius: 5px;
  outline: none;
  :active {
    box-shadow: 0 5px rgba(255, 255, 255, 0.3);
    transform: translateY(2px);
  }
`;
const PageFunction = styled.div`
  margin: 50px auto;
  @media (max-width: 660px) {
    margin: 100px auto;
  }
`;

function Post({ post }) {
  return (
    <PostContainer>
      <PostTitle to={`/post/${post.id}`}>{post.title}</PostTitle>
      <PostDate>{new Date(post.createdAt).toLocaleString()}</PostDate>
    </PostContainer>
  );
}

Post.propTypes = {
  post: PropTypes.object,
};

export default function HomePage() {
  const [posts, setPosts] = useState([]);
  const [totalPage, setTotalPage] = useState();
  const [page, setPage] = useState(1);
  const [isLoading, setIsLoading] = useState(false);
  const itemPerPage = 5;

  useEffect(() => {
    getPosts(page, itemPerPage).then((post) =>
      setTotalPage(Math.ceil(post.headers.get("X-Total-Count") / itemPerPage))
    );

    setIsLoading(true);
    getPosts(page, itemPerPage)
      .then((res) => res.json())
      .then((posts) => {
        setIsLoading(false);
        setPosts(posts);
      });
  }, [page]);

  const handleFirstPage = () => {
    pageChanged(1, itemPerPage);
  };

  const handleLastPage = () => {
    pageChanged(totalPage, itemPerPage);
  };
  const handlePreviousPage = () => {
    if (page !== 1) {
      pageChanged(page - 1, itemPerPage);
    }
  };

  const handleNextPage = () => {
    if (page !== totalPage) {
      pageChanged(page + 1, itemPerPage);
    }
  };

  function pageChanged(newSet, itemPerPage) {
    if (isLoading) {
      return;
    }
    setPage(newSet);
    setIsLoading(true);
    getPosts(newSet, itemPerPage)
      .then((res) => res.json())
      .then((posts) => {
        setIsLoading(false);
        setPosts(posts);
      });
  }

  return (
    <Root>
      {isLoading && <Loading>Loading....</Loading>}
      {posts.map((post) => (
        <Post key={post.id} post={post} />
      ))}
      <PageFunction>
        <Button onClick={handleFirstPage}>First</Button>
        <Button onClick={handlePreviousPage}>Pervious</Button>
        <PageSign>
          {page} / {totalPage}
        </PageSign>
        <Button onClick={handleNextPage}>Next</Button>
        <Button onClick={handleLastPage}>Last</Button>
      </PageFunction>
    </Root>
  );
}
