import React, { useContext } from "react";
import styled from "styled-components";
import { Link, useLocation, useHistory } from "react-router-dom";
import { AuthContext } from "../../contexts";
import { setAuthToken } from "../../utils";

const HeaderContainer = styled.div`
  height: 10%;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  top: 0px;
  right: 0px;
  background: rgba(0, 0, 0, 0.35);
  box-sizing: border-box;
  @media (max-width: 660px) {
    flex-direction: column;
    height: auto;
    margin-bottom: 50px;
    background: rgba(0, 0, 0, 1);
  }
`;
const Brand = styled.span`
  font-size: 25px;
  margin-left: 20px;
  margin-right: 40px;
  color: rgba(255, 255, 255, 0.8);
`;
const Block = styled(Link)`
  position: relative;
  margin: 0px 20px;
  font-size: 14px;
  text-decoration: none;
  color: rgba(255, 255, 255, 0.5);
  cursor: pointer;

  :hover {
    color: white;
  }
  ::after {
    content: "";
  }
  @media (max-width: 660px) {
    margin: 10px 10px;
    border: 1px solid white;
    padding: 3px;
    border-radius: 3px;
  }

  ${(props) =>
    props.$active &&
    `
    color:white;
    ::after {
      content:"";
      width:100%;
      position:absolute;
      bottom:0;
      left:0;
      height:0.5px;
      background:white;
      transform: scaleX(0);
      transform-origin: right center;
      transition: transform .6s;
      transition-duration: 1s;
    }
    
    `}
`;

const NavSection1 = styled.div`
  display: flex;
  align-items: center;
  margin-right: auto;
  @media (max-width: 660px) {
    margin: auto;
  }
`;

const NavSection2 = styled.div`
  display: flex;
`;

export default function Header() {
  const history = useHistory();
  const location = useLocation(); //現在處於的 url
  const { user, setUser } = useContext(AuthContext);

  const handleLogout = () => {
    setAuthToken("");
    setUser("");
    if (location.pathname !== "/") {
      history.push("/");
    }
  };

  return (
    <HeaderContainer>
      <Brand>This is your first blog</Brand>
      <NavSection1>
        <Block to="/" $active={location.pathname === "/"}>
          Home
        </Block>
        <Block to="/aboutMe" $active={location.pathname === "/aboutMe"}>
          About me
        </Block>
        {user && (
          <Block to="/newPost" $active={location.pathname === "/newPost"}>
            Release
          </Block>
        )}
      </NavSection1>
      <NavSection2>
        <Block to="/register" $active={location.pathname === "/register"}>
          Register
        </Block>
        {!user && (
          <Block to="/login" $active={location.pathname === "/login"}>
            Login
          </Block>
        )}
        {user && <Block onClick={handleLogout}>Logout</Block>}
      </NavSection2>
    </HeaderContainer>
  );
}
